@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@500&display=swap');

.navbar {
    height: 100px;
    padding: 20px;
    position: sticky;
    top: 0;
    background-color: rgba(0, 0, 0, 0.253);
    border-bottom: 1px solid #80808041;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.157);
    margin-bottom: -100px;
}

.navbar .logo-container {
    width: fit-content;
}

.navbar .logo-link {
    display: block;
    width: fit-content;
    padding: 0 10px;
    text-decoration: none;
    color: white;
}

.navbar .logo-link h1 {
    font-size: 3rem;
    font-family: 'Cormorant', serif;
}
.navbar .logo-link h1:hover {
    text-shadow: 1px 2px rgb(0, 0, 0);
}


.navbar-black-bg {
    background-color: #000000ea;
}