@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@500&display=swap');

.results-page {
    overflow: hidden;
}

.results-page .results-banner {
    background-image: url("../../public/assets/bannerimg1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    color: white;
    margin-bottom: 50px;
}

.results-page .results-banner p {
    font-family: 'Cormorant', serif;
    font-size: 4rem;
    padding: 0 40px;
    padding-top: 25vh;
    padding-bottom: 5vh;
}

.results-page .results-container {
    width: fit-content;
    margin: 0 auto;
    margin-bottom: 15vh;
}

.results-page .results-container .title {
    font-family: 'Cormorant', serif;
    font-size: 4rem;
    text-align: center;
    border-bottom: 1px solid black;
    margin-bottom: 50px;
}

.results-page .results-container .table-container {
    max-height: 70vh;
    overflow-y: auto;
    border: 2px solid rgba(24, 21, 13, 0.267);
}
.results-page .results-container .student-table thead {
    background-color: #16131034;
}
.results-page .results-container .student-table thead th {
    padding: 10px 5px;
}

.results-page .results-container .student-table tbody {
    background-color: #f0ece683;
}

.results-page .results-container .student-table tbody td {
    padding: 5px 10px;
    max-width: 250px;
}

.results-page .results-container .student-table tbody tr:hover {
    background-color: #ffffffab;
}

.results-page .results-container .student-table tbody .edit-btn, .delete-btn {
    width: 20px;
    height: 20px;
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
}

.results-page .results-container .student-table tbody .edit-btn {
    background-image: url("../../public/assets/edit-btn.jpg");
    background-size: contain;
    background-repeat: no-repeat;
}
.results-page .results-container .student-table tbody .delete-btn {
    background-image: url("../../public/assets/delete-btn.jpg");
    background-size: contain;
    background-repeat: no-repeat;
}

.results-page .invalid-request {
    border: 2px solid rgba(24, 21, 13, 0.267);
    background-color: #ffffffab;
    padding: 100px 25px;
    text-align: center;
}
.results-page .invalid-request h3 {
    font-size: 1.5rem;
    margin-bottom: 50px;
}


.confirmation .modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
    width: 400px;
    background: #f0eeeb;
    border: 1px solid #626160;
    padding: 5px 5px 30px 5px;
    border-radius: 5px;
}
.confirmation .close-btn-container {
    width: fit-content;
    margin-right: 0;
    margin-left: auto;
}
.confirmation .close-btn {
    width: 20px;
    cursor: pointer;
    background: none;
    outline: none;
    border: 0;
    font-size: 20px;
    font-weight: bold;
}
.confirmation .confirm-prompt {
    text-align: center;
    font-weight: bold;
    margin-bottom: 20px;
}
.confirmation .bg-close {
    background-color: #00000052;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    width: 100vw;
    height: 100vh;
}
.confirmation .confirm-btn-container {
    width: fit-content;
    margin: 0 auto;
}
.confirmation .confirm {
    cursor: pointer;
    padding: 5px;
}