@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@500&display=swap');

.registration {
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
}

.registration h1 {
    background-color: black;
    width: 100%;
    display: block;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    color: white;
    font-family: 'Cormorant', serif;
    font-size: 3rem;
    padding: 25px 0;
    margin-bottom: 25px;
}

.registration .banner {
    background-image: url("../../public/assets/bannerimg2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    color: white;
}

.registration .banner p {
    font-size: 4.5rem;
    font-family: 'Cormorant', serif;
    padding: 0 40px;
    padding-top: 25vh;
    padding-bottom: 5vh;
}


.form-panel {
    width: 50%;
    margin: 0 auto;
    background-color: white;
    padding: 25px 50px;
    border: 1px solid rgb(200, 200, 200);
    margin-bottom: 10vh;
}

.form-panel .form-title {
    text-align: center;
    margin-bottom: 50px;
}
.form-panel .form-title h3 {
    font-size: 2.5rem;
    font-family: 'Cormorant', serif;
}

.form-panel .student-form {
    width: fit-content;
    margin: 0 auto;
}

.form-panel .form-field  {
    margin-bottom: 25px;
    border-bottom: 1px solid rgb(46, 46, 46);
}
.form-panel .form-field:nth-child(6)  {
    margin-bottom: 50px;
}
.form-panel .form-field:last-child  {
    border: none;
    margin-bottom: 50px;
    width: fit-content;
    margin-right: 0;
    margin-left: auto;
}


@keyframes buttonTransition {
    from { background-color: black; color: white;}
    to {background-color: rgb(228, 228, 228); color: black;}
}
.form-panel .form-field:last-child button  {
    display: inline-block;
    background-color: black;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
    border: 0;
    border-radius: 25px;
    font-family: 'Cormorant', serif;
    font-size: 1.5em;
}
.form-panel .form-field:last-child button:hover  {
    display: inline-block;
    animation-name: buttonTransition;
    animation-duration: 0.3s;
    animation-fill-mode: forwards;
}

.form-panel .form-field .field-label {
    display: inline-block;
    width: 160px;
    margin-right: 15px;
    font-size: 15px;
    font-weight: 100;
}

.form-panel .form-field input {
    outline: none;
    border: none;
}