@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@500&display=swap');

.footer {
    color: white;
    background-color: black;
    padding: 50px 0;
}

.footer .logo {
    margin: 0 auto;
    width: fit-content;
    text-align: center;
}

.footer a {
    text-decoration: none;
    color: white;
}

.footer h1 {
    font-family: 'Cormorant', serif;
    font-size: 3em;
    text-transform: uppercase;
}