@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@500&display=swap');

.home-container {
    font-family: 'Cormorant', serif;
}

.home-container .banner {
  background-image: url("../../public/assets/bgimg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  color: white;
  padding: 50px;
}

.home-container .banner .banner-title, .banner-subtitle {
    font-family: 'Cormorant', serif;
    text-shadow: 2px 2px 5px black;
}

.home-container .banner .banner-title {
    padding-top: 40vh;
    font-size: 6rem;
    margin-bottom: 10px;
}

.home-container .banner .banner-subtitle {
    font-size: 2rem;
    padding-bottom: 40vh;
}

.home-container .main-content {
    margin: 0 auto;
    margin-top: 50px;
    max-width: 90%;
    margin-bottom: 12vh;
}

.home-container .main-title {
    text-align: center;
    border-bottom: 1px solid #0000002d;
    margin-bottom: 20px;
}
.home-container .main-content .main-title h1 {
    font-family: 'Cormorant', serif;
    font-size: 4rem;
    margin-bottom: 14px;
}

.home-container .panels-container {
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    width: 50%;
    background-color: #fafafa;
    padding: 30px;
    border: 1px solid #c3c3c3;
}

.home-container .panels-container .left-side {
    flex-basis: 50%;
    margin: 0 auto;
    border-right: 1px solid black;
}
.home-container .panels-container .right-side {
    flex-basis: 50%;
    margin: 0 auto;
    margin-left: 50px;
}

.home-container .panels-container .section h3 {
    font-family: 'Cormorant', serif;
    font-size: 3em;
    margin-bottom: 20px;
}
.home-container .panels-container .section {
    margin-bottom: 35px;
}

.home-container .panels-container .left-side .links {
    margin-left: 50px;
    width: fit-content;
}
.home-container .panels-container .left-side .links ul {
    list-style-type: none;
    line-height: 2em;
    font-size: 1.1em
}


.home-container .panels-container .section .search-field {
    margin-bottom: 18px;
}

.home-container .panels-container .section .search-field input, button,
.home-container .panels-container .section .edit-student-panel input, button {
    display: inline-block;
    vertical-align: middle;
}
.home-container .panels-container .section .search-field input, 
.home-container .panels-container .section .edit-student-panel input {
    margin-right: 10px;
    height: 30px;
    border-radius: 15px;
    padding: 0 14px;
}
.home-container .panels-container .section .search-field input:hover, 
.home-container .panels-container .section .edit-student-panel input:hover {
    border-color: black;
}

.home-container .panels-container .section .search-field button,
.home-container .panels-container .section .edit-student-panel button {
    background-color: black;
    color: white;
    width: 150px;
    padding: 0 10px;
    cursor: pointer;
    border: 0;
    height: 30px;
    border-radius: 15px;
}

@keyframes buttonTransition {
    from { background-color: black; color: white;}
    to {background-color: rgb(228, 228, 228); color: black;}
}
.home-container .panels-container .section .search-field button:hover, 
.home-container .panels-container .section .edit-student-panel button:hover {
    animation-name: buttonTransition;
    animation-duration: 0.3s;
    animation-fill-mode: forwards;
}


